.movies {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 50px;
  padding-top: 70px;
  width: 80%;
}

.movies .movie {
  width: 45%;
  background-color: white;
  margin-bottom: 70px;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  color: #adaeb9;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movies .movie a {
  display: grid;
  grid-template-columns: minmax(150px, 1fr) 2fr;
  grid-gap: 20px;
  text-decoration: none;
  color: inherit;
}

.movie img {
  position: relative;
  max-width: 150px;
  width: 100%;
  top: -50px;
  margin-right: 30px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie img:before {
  content: ' ';
  display: block;
  position: absolute;
  height: 225px;
  width: 100%;
  background-image: url('../default_poster.jpg');
  background-size: cover;
  box-sizing: border-box;
}

.movie .movie__title,
.movie .movie__year {
  font-weight: 300;
  margin: 0;
}

.movie .movie__title {
  margin-bottom: 5px;
  font-size: 24px;
  color: #2c2c2c;
}

.movie .genres {
  list-style-type: none;
  padding: 0;
  margin: 5px 0;
  display: flex;
  flex-wrap: wrap;
}

.genres li,
.movie .movie__year {
  font-size: 14px;
  margin-right: 10px;
}

.movie .movie__column {
  width: 80%;
}

@media screen and (max-width: 415px) {
  .movies {
    width: 100%;
    padding: 70px 0 0 0;
  }

  .movies .movie {
    width: 100%;   
  }

  .movies .movie a {
    display: flex;
    flex-direction: column;
  }

  .movies a:first-child {
    display: flex;
    align-items: center;
  }
  
  .movie img {
    margin: 0;    
  }
}

@media screen and (max-width: 1024px) {
  .movies {
    width: 90%;
  }

  .movies .movie {
    width: 100%;    
  }

  .movie .movie__column {
    width: 100%;
  }
}